import { liteClient as algoliasearch } from 'algoliasearch/lite';
import instantsearch from 'instantsearch.js';
import {
  configure,
  searchBox,
  stats,
  hits,
  pagination,
  refinementList,
  rangeSlider,
  geoSearch,
} from 'instantsearch.js/es/widgets';

import 'instantsearch.css/themes/reset.css';

const search = instantsearch({
  searchClient: algoliasearch('5UZ8QHNKCM', 'ce7ac333aa3cfd4d75977c4eb9e4eaf8'),
  indexName: 'stayget',
  routing: true,
  insights: true,
});

const InfoWindow = new window.google.maps.InfoWindow({
  pixelOffset: new window.google.maps.Size(0, -30),
});

search.addWidgets([
  configure({
    aroundLatLngViaIP: true,
    hitsPerPage: 3,
  }),

  searchBox({
    container: '#search-box',
    placeholder: 'Gdzie chcesz jechać?',
    showSubmit: false,
    cssClasses: {
      input: 'form-control',
    },
  }),

  stats({
    container: '#stats',
  }),

  hits({
    container: '#hits',
    templates: {
      item(hit, { html, components }) {
        return html`
          <div class="hit col-sm-12">
          <div class="col-sm-5">
            <img class="picture" src="${hit.picture_url}" />
          </div>
            <div class="col-sm-7">
              <h4 class="media-heading">
                ${components.Highlight({ hit, attribute: 'name' })}
              </h4>
              <p>
              Gwiazdki: ${hit.stars}
              </p>
              <p>
              Miasto: ${hit.city}
              </p>
            </div>
          </div>
        `;
      },
      empty({ query }, { html }) {
        return html`<div class="text-center">
          No results found matching <strong>${query}</strong>.
        </div>`;
      },
    },
  }),

  pagination({
    container: '#pagination',
    scrollTo: '#results',
    cssClasses: {
      list: 'pagination',
      selectedItem: 'active',
    },
    showFirst: false,
    showPrevious: false,
    showNext: false,
    showLast: false,
  }),

  refinementList({
    container: '#city',
    attribute: 'city',
    sortBy: ['name:asc'],
    cssClasses: {
      item: ['col-sm-3'],
    },
  }),

  refinementList({
    container: '#stars',
    attribute: 'stars',
    sortBy: ['name:asc'],
    cssClasses: {
      item: ['col-sm-3'],
    },
  }),

  rangeSlider({
    container: '#price',
    attribute: 'price',
    pips: false,
    tooltips: {
      format(rawValue) {
        return `${parseInt(rawValue, 10)}zł`;
      },
    },
  }),


  geoSearch({
    container: '#map',
    googleReference: window.google,
    customHTMLMarker: {
      createOptions: () => ({
        anchor: {
          x: 0,
          y: 5,
        },
      }),
      events: {
        click: ({ item, marker, map }) => {
          // @ts-expect-error .storybook/utils/create-info-box.js
          if (InfoWindow.getMap()) {
            InfoWindow.close();
          }

          InfoWindow.setContent(item.name);

          InfoWindow.open(map, marker);
        },
      },
    },
    templates: {
      HTMLMarker: (hit, { html }) =>
        html`<div class="my-custom-marker">
            ${hit.price} zł
          </div>`,
    }
  }),



]);

search.start();
